ul, li {
    text-decoration: none;
    list-style: none;
}
.mv-main {
    width: 100%;
    height: 100vh;
    background: linear-gradient(#fff, #000);
    margin-top: 1rem;
    .mv-logo {
        img {
            position: absolute;
            left: 8rem;
            top: 2rem;
        }
    }
    .mv-wrap { 
        max-width: 1200px;
        margin: 8rem;
        padding: 0 10%;
        .mv-wrap-top {
            display: flex;
            justify-content: space-between;
            // margin: 30px auto 0 8rem;
            .mv-wrap-img {
                display: flex;
                align-items: center;
                img {
                    width: 15.3125rem;
                    height: 22rem;
                    border: 1px solid #000;
                }
            }
            .mv-context {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-top: 1rem;
                margin-left: 3rem;
                // width: 50%;
                padding-bottom: 1rem;
                position: relative;
                h1 {
                    text-align: left;
                    font-size: 2.5rem;
                }
                h2 {
                    font-size: 1.2rem;
                    color: rgba($color: #000, $alpha: 0.7);
                    margin-bottom: 1.2rem;
                }
                p {
                    color: #fff;
                    font-weight: 550;
                }
                button {
                    border: 1px solid gray;
                    padding: 10px;   
                    margin: 1rem 0;
                }
            }
        }
    }
    
    .mv-review {
        margin-top: 4rem;
        // display: flex;
        // flex-direction: column;
        // align-items: flex-start;
        h2 {
            width: 100%;
            font-size: 1.2rem;
            margin-bottom: 1.2rem;
            text-align: left;
        }
        .mv-review-input {
            width: 100%;
            text-align: center;
            position: relative;
            display: flex;
            input {
                width: 100%;
                padding: 20px;
                padding-left: 30px;
                height: 5rem;
                border-radius: 10px;
                background-color: #ccc;
            }
            button {
                position: absolute;
                right: 20px;
                top: 20px;
                padding: 30px 5px;
                border-radius: 10px;
                background-color: bisque;
            }
        }
        ul {
            margin-top: 1.2rem;
            li {
                border: 1px solid #aaa;
                border-radius: 10px;
                padding: 30px;
                background-color: #fff;
                text-align: left;
            }
        }

    }
    
}